const API_URL = '/api'

// Ëé∑ÂèñÁà±ÂøÉËÆ°Êï∞
export async function getLoveCount() {
  try {
    const response = await fetch(`${API_URL}/love/count`)
    const data = await response.json()
    return data.count
  } catch (error) {
    console.error('Ëé∑ÂèñÁà±ÂøÉËÆ°Êï∞Â§±Ë¥•:', error)
    throw error
  }
}

// Â¢ûÂä†Áà±ÂøÉËÆ°Êï∞
export async function incrementLove() {
  try {
    const response = await fetch(`${API_URL}/love/increment`, {
      method: 'POST'
    })
    const data = await response.json()
    return data.count
  } catch (error) {
    console.error('Â¢ûÂä†Áà±ÂøÉËÆ°Êï∞Â§±Ë¥•:', error)
    throw error
  }
}

